	get works() {
		const search = this.state.search.toLowerCase();
		let results = [];
		let nums = 0;

		works.map(workId => {
			nums += 1;
		});

		for(let i=0;i<nums;i++) {
			results.push({
				"id": works.id,
				"name": WorkSelectEN(works.school) + " " + works.date,
				"theme": works.theme,
				"img": {
					"is": works.img.is,
					"link": works.img.link,
					"size": works.img.size
				},
			});
		}

		return results.filter(({ name }) => name.toLowerCase().indexOf(search) > -1);
	}

